@import "./var.scss";
.app {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: #f5f6f9;
  .nav {
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%;
    height: 64px;
    padding: 14px 20px;
    background: #edf0f3;
    background-color: rgba(0, 0, 0, 0);
    .LOGO {
      width: 182px;
      height: 37px;
      background-image: url(./assets/logo_zegocloud@2x.png);
      background-size: contain;
    }
    .link {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .link_item {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 4px;
      padding: 6px 10px;
      color: #666666;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      text-decoration: none;
      cursor: pointer;
      span {
        width: 20px;
        height: 20px;
        margin-right: 6px;
      }
      &:hover {
        color: #0055ff;
        .icon__doc {
          background-image: url("./assets/icon_doc_hover.png");
        }
        .icon__github {
          background-image: url("./assets/icon_github_hover.png");
        }
      }
    }
    .icon__doc {
      background: url("./assets/icon_doc.png") no-repeat center/cover;
    }
    .icon__github {
      background: url("./assets/icon_github.png") no-repeat center/cover;
    }
    .icon_settings {
      background: url("./assets/icon_settings.png") no-repeat center/cover;
    }
  }
  .myMeeting {
    flex: 1 0 auto;
    width: 100%;
    // height: calc(100vh - 64px);
  }
  .serviceTips {
    position: fixed;
    position: absolute;
    bottom: 10px;
    left: 50%;
    flex: 0 0 auto;
    width: 100%;
    color: #ababb5;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    transform: translateX(-50%);
    a {
      color: #0055ff;
      text-decoration: none;
    }
  }
  .pcServiceTips {
    position: relative;
    margin-top: 10px;
  }
  .pcSettingsModel {
    position: fixed;
    width: 100%;
    height: 100%;
    .settingsWrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 306px;
      height: 319px;
      background: #ffffff;
      border: 1px solid #dde0e3;
      border-radius: 8px;
      box-shadow: 0px 3px 6px 0px rgba(79, 79, 83, 0.2);
      transform: translate(-50%, -50%);
      .settingsHeader {
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-sizing: border-box;
        width: 100%;
        height: 44px;
        padding: 0 20px;
        background: #ffffff;
        border-bottom: 1px solid #edeff3;
        border-radius: 8px 8px 0px 0px;
        p {
          color: #2a2a2a;
          font-weight: 600;
          font-size: 14px;
          font-family: PingFangSC-Semibold, PingFang SC;
          line-height: 16px;
        }
        .settingsClose {
          width: 18px;
          height: 18px;
          background: url("./assets/icon_settings_close.png") no-repeat
            center/cover;
          cursor: pointer;
          &:hover {
            background: url("./assets/icon_settings_close_hover.png") no-repeat
              center/cover;
          }
        }
      }
      .settingsBody {
        box-sizing: border-box;
        width: 100%;
        padding: 26px;
        .settingsMode {
          margin-bottom: 24px;
          padding: 0 16px;
          color: #666666;
          font-weight: 400;
          font-size: 12px;
          font-family: PingFangSC-Regular, PingFang SC;
          line-height: 17px;
        }
      }
      .settingsModeItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 32px;
        padding: 0 16px;
        border-radius: 6px;
        cursor: pointer;
        &:not(:last-of-type) {
          margin-bottom: 18px;
        }
        &:hover {
          background: #f5f7fa;
        }
        p {
          color: #2a2a2a;
          font-weight: 400;
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          line-height: 20px;
        }
        span {
          width: 15px;
          height: 16px;
          background: url("./assets/icon_check_off@2x.png") no-repeat
            center/cover;
        }
        &.settingsModeItemSelected {
          p {
            color: #0055ff;
          }
          span {
            background: url("./assets/icon_check_on@2x.png") no-repeat
              center/cover;
          }
        }
      }
      .settingsBtn {
        width: 222px;
        height: 32px;
        margin: 24px auto 0;
        color: #ffffff;
        font-weight: 500;
        font-size: 14px;
        font-family: PingFangSC-Medium, PingFang SC;
        line-height: 32px;
        text-align: center;
        background: #0055ff;
        border-radius: 6px;
        cursor: pointer;
      }
    }
  }
}

.mobileApp {
  overflow-x: hidden;
  background: #ffffff;
  .mobileNav {
    // position: absolute;
    z-index: 102;
    height: vw(108px);
    padding: vw(27px) vw(30px);
    background: #ffffff;

    .mobileLOGO {
      width: vw(248px);
      height: vw(54px);
      background-image: url(./assets/App_logo@2x.png);
      background-size: contain;
    }
    .mobileLink {
      a {
        margin: 0;
        padding: 0px;
      }
      a:not(:last-child) {
        margin-right: vw(40px);
      }
      span {
        width: vw(50px);
        height: vw(50px);
        margin-right: 0;
      }
    }
  }
  .mobileMeeting {
    // flex: 0;
    margin-top: vw(50px);
  }
  .mobileServiceTips {
    position: relative;
    z-index: 101;
    flex: 1;
    box-sizing: border-box;
    padding: vw(94px) vw(146px) vw(12px) vw(146px);
    color: #ababb5;
    // font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    font-size: vw(24px);
    line-height: vw(33px);
    white-space: nowrap;
    background-color: #ffffff;
  }
  .mobileSettingsModel {
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.3);

    .settingsWrapper {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      overflow: hidden;
      background: #ffffff;
      border-radius: vw(46px) vw(46px) 0px 0px;
      .settingsHeader {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: flex-end;
        box-sizing: border-box;
        height: vw(98px);
        padding: vw(14px) vw(23px);
        background: #ffffff;
        border-radius: vw(46px) vw(46px) 0px 0px;
        p {
          color: #2a2a2a;
          font-weight: 500;
          font-size: vw(36px);
          font-family: PingFangSC-Medium, PingFang SC;
          line-height: vw(70px);
        }
        .settingsClose {
          width: vw(70px);
          height: vw(70px);
          margin-right: vw(16px);
          background: url("./assets/icon_close_down_m.png") no-repeat
            center/cover;
          cursor: pointer;
        }
      }
      .settingsBody {
        box-sizing: border-box;
        width: 100%;
        padding-bottom: vw(48px);
      }
      .settingsModeItem {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: vw(29px) vw(26px) vw(29px) vw(36px);
        cursor: pointer;
        p {
          color: #2a2a2a;
          font-weight: 400;
          font-size: vw(30px);
          font-family: PingFangSC-Regular, PingFang SC;
          line-height: vw(42px);
        }
        span {
          width: vw(56px);
          height: vw(56px);
        }
        &.settingsModeItemSelected {
          p {
            color: #0055ff;
          }
          span {
            background: url("./assets/icon_checked_m.png") no-repeat
              center/cover;
          }
        }
      }
    }
  }
}
